import Container from "./Container";
import img1 from "../assets/hero-banner.png";
import Image from "next/image";

const TouristAgency = () => {
  return (
    <Container className="grid md:grid-cols-2 lg:grid-cols-2 bg-zinc-500 justify-between gap-7">
      {/* left side */}
      <div className="bg-slate-600"></div>
      {/* right side */}
      <div className="bg-gray-600">
        <Image src={img1} alt="image" height={500} width={500} />
      </div>
    </Container>
  );
};

export default TouristAgency;
